<template>
  <div class="saleList">
     
    <div class="adWrapper">
      <advertisement></advertisement>
    </div>
    
    
    <ul>
      <li v-for="(item,index) in goodList" class="goodItem" @click="sendParams(index)">
          <goodCard v-bind:good="item"></goodCard>
      </li>
    </ul>
    
    <laypage :pages="pages" @changePage="getGoodList"></laypage>
  </div>
</template>

<script>
import { bus } from '../../assets/bus.js';
import goodCard from '../goodCard/goodCard.vue';
import laypage from '../laypage/laypage.vue';
import advertisement from '../advertisement/advertisement.vue';
export default {
  data(){
    return{
      goodListUrl:"/dachuang/sale",
      goodList:[],
      pages:1,
      currentType:0,
      currentId:0
    }
  },
  created(){
    var vm=this;
    vm.getGoodList(1);
    bus.$on("isGetGoodList",function(type){
      vm.currentType=type;
      vm.getGoodList(1);
    })
  },
  methods:{
    getGoodList:function(page){
      var url=this.goodListUrl+"?type="+this.currentType+"&page="+page;
     // var url=this.goodListUrl+type+".json";
      this.$http.get(url).then(function(res){
        console.log(res.body);
        this.goodList=res.body.data;
        this.pages=parseInt(res.body.page);
      },function(err){
        console.log(err);
      })
      
    },
    sendParams:function(index){
      var id=this.goodList[index].id;
      this.$router.push('/sale/good?id='+id);
    }
  },
  components:{
    "goodCard":goodCard,
    "laypage":laypage,
    "advertisement":advertisement
  }
}
</script>

<style scoped>
  .adWrapper{
    margin: 20px 0;
  }
  .saleList{
    width: 95%;
    display: inline-block;
    padding-left: 0%;
    text-align:left;
  }
  .saleList li{
     display: inline-block;
     margin: 20px;
     list-style: none;
  }
  .saleList ul{
    padding: 20px 20px 80px 20px;
  }
</style>


