<template>
  <div class="laypage">
    <div v-if="pages>=1" class="page">
      <a @click="goFirstPage">首页</a>
      <a @click="goPeviousPage" v-show="currentPage>1">上一页</a>
      <ul class="pageList">
        <li v-for="item in pages" v-show="Math.abs(currentPage-item)<4" @click="getCurrenPage(item)">{{item}}</li>
      </ul>
      <a @click="goNextPage" v-show="currentPage<pages.length">下一页</a>
      <a @click="goLastPage">末页</a>
    </div>
  </div>
</template>

<script>
export default {
  props:["pages"],
  data(){
    return {
      currentPage:1,
      goodsUrl:"http://localhost:3000/sale"
    }
  },
  methods:{
    getCurrenPage:function(page){
      this.currentPage=page;
      this.$emit('changePage',this.currentPage);
    },
    goFirstPage:function(){
      this.currentPage=1;
      this.$emit('changePage',this.currentPage);
    },
    goLastPage:function(){
      this.currentPage=this.pages;
      this.$emit('changePage',this.currentPage);
    },
    goNextPage:function(){
      this.currentPage++;
      this.$emit('changePage',this.currentPage);
    },
    goPeviousPage:function(){
      this.currentPage--;
      this.$emit('changePage',this.currentPage);
    }
  }

}
</script>

<style scoped>
  .laypage{
    width: 100%;
    text-align: center;
  }
  .pageList{
    margin: 20px auto;
    padding: 0;
    display: inline-block;
    border-radius: 5px;
  }
  .pageList li{
    width: 25px;
    height: 25px;
    margin: 0px;
    line-height: 25px;
    display: inline-block;
    border: 1px solid rgba(0,0,0,0.25);
    background: rgba(0,0,0,0.1);
    list-style: none;
    
  }
  .pageList li:hover{
    cursor: pointer;
    background: rgba(0,0,0,0.5);
  }
  .page a{
    height: 27px;
    margin: 0px;
    padding: 0px;
    vertical-align: middle;
    display: inline-block;
    background: rgba(0,0,0,0.1);
    border-radius: 5px;
  }
  
  .page a:visited{
    background: rgba(0,0,0,0.5);
  }
  .page a:hover{
    cursor: pointer;
  }
  .active{
    background: rgba(0,0,0,0.5);
  }
</style>


